import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    scrollBehavior () {
        return { x: 0, y: 0 }
    },
    routes: [

        {
    // =============================================================================
    // MAIN LAYOUT ROUTES
    // =============================================================================
            path: '',
            component: () => import('./layouts/main/Main.vue'),
            children: [
        // =============================================================================
        // Theme Routes
        // =============================================================================
              {
                path: '/',
                name: 'home',
                component: () => import('./views/pagesGeneral/Home.vue')
              },
              {
                path: '/listado',
                name: 'listado',
                component: () => import('./views/listView/Listado.vue')
              },
              {
                path: '/crear',
                name: 'crear',
                component: () => import('./views/createView/Crear.vue')
              },
              {
                path: '/estadisticas/:idCampana',
                name: 'estadistica',
                component: () => import('./views/statisticsView/Estadisticas.vue')
              },
            ],
        },
    // =============================================================================
    // FULL PAGE LAYOUTS
    // =============================================================================
        {
            path: '',
            component: () => import('@/layouts/full-page/FullPage.vue'),
            children: [
        // =============================================================================
        // PAGES
        // =============================================================================
              {
                path: '/login',
                name: 'login',
                component: () => import('@/views/pagesGeneral/Login.vue')
              },
              {
                path: '/error-404',
                name: 'error',
                component: () => import('@/views/pagesGeneral/Error404.vue')
              },
              {
                path: '/logout',
                name: 'logout',
                component: () => import('@/views/pagesGeneral/Logout.vue')
              },
            ]
        },
        // Redirect to 404 page, if no match found
        {
            path: '*',
            redirect: '/error-404'
        }
    ],
})


//Para bloquear todas las rutas, slo cambiando el parametro de next por un false
router.beforeEach((to, from, next)=>{
  const publicPages = ['/login', '/error-404']; //Paginas bloqueadas si no detecta que est bloqueado
  const LoginPages = ['/login'];
  //const publicPages = ['/login', '/', '/error-404', '/crearConcepto', '/crearUsuario', '/listadoConceptos', '/listadoUsuarios'];
  const authRequired = !publicPages.includes(to.path);
  const authRequired2 = !LoginPages.includes(to.path);
  const loggedIn = localStorage.getItem('_token');
  // trying to access a restricted page + not logged in
  // redirect to login page
  if (authRequired && !loggedIn) {
    next('/login');
  }else if(!authRequired2 && loggedIn){
    next('/');
  } else {
    next();
  }
});

router.afterEach(() => {
  // Remove initial loading
  const appLoading = document.getElementById('loading-bg')
    if (appLoading) {
        appLoading.style.display = "none";
    }
})

export default router
